 background-color: #fff;\n  }\n\n  button,\n  input,\n  textarea {\n    border: 1px solid #000;\n  }\n\n  body,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6,\n  pre,\n  code,\n  button,\n  input,\n  textarea,\n  footer,\n  summary,\n  strong {\n    color: #000;\n  }\n\n  summary::marker {\n    color: #000;\n  }\n\n  summary::-webkit-details-marker {\n    color: #000;\n  }\n\n  tbody tr:nth-child(even) {\n    background-color: #f2f2f2;\n  }\n\n  a {\n    color: #00f;\n    text-decoration: underline;\n  }\n}\n","$range-slider-shadow: 0 1px 1px #000, 0 0 1px #0d0d0d;\n\n@mixin range-track() {\n  width: 100%;\n  height: 9.5px;\n  transition: 0.2s;\n  background: var(--background-main);\n  border-radius: 3px;\n}\n@mixin range-thumb() {\n  -webkit-appearance: none;\n  height: 20px;\n  width: 20px;\n  border-radius: 50%;\n  background: var(--border);\n  margin-top: -7px;\n}\n\ninput[type=\"range\"] {\n  margin: 10px 0;\n  padding: 10px 0;\n  background: transparent;\n}\n\ninput[type=\"range\"]:focus {\n  outline: none;\n}\n\ninput[type=\"range\"]::-webkit-slider-runnable-track {\n  @include range-track();\n}\ninput[type=\"range\"]::-moz-range-track {\n  @include range-track();\n}\ninput[type=\"range\"]::-ms-track {\n  @include range-track();\n}\n\ninput[type=\"range\"]::-webkit-slider-thumb {\n  @include range-thumb();\n}\ninput[type=\"range\"]::-moz-range-thumb {\n  @include range-thumb();\n}\ninput[type=\"range\"]::-ms-thumb {\n  @include range-thumb();\n}\n\ninput[type=\"range\"]:focus::-webkit-slider-thumb {\n  box-shadow: $range-slider-shadow;\n}\ninput[type=\"range\"]:focus::-moz-range-thumb {\n  box-shadow: $range-slider-shadow;\n}\ninput[type=\"range\"]:focus::-ms-thumb {\n  box-shadow: $range-slider-shadow;\n}\n\ninput[type=\"range\"]:focus::-webkit-slider-runnable-track {\n  background: var(--background-main);\n}\ninput[type=\"range\"]:focus::-ms-fill-lower {\n  background: var(--background-main);\n}\ninput[type=\"range\"]:focus::-ms-fill-upper {\n  background: var(--background-main);\n}\n\n// Extra MS overrides\ninput[type=\"range\"]::-ms-track {\n  background: transparent;\n  border-color: transparent;\n  border-width: 16px 0;\n  color: transparent;\n}\ninput[type=\"range\"]::-ms-fill-lower {\n  background: var(--background-main);\n  border: 0.2px solid #010101;\n  border-radius: 3px;\n  box-shadow: 1px 1px 1px #000, 0 0 1px #0d0d0d;\n}\ninput[type=\"range\"]::-ms-fill-upper {\n  background: var(--background-main);\n  border: 0.2px solid #010101;\n  border-radius: 3px;\n  box-shadow: 1px 1px 1px #000, 0 0 1px #0d0d0d;\n}\n","@import \"~novo-design-tokens/scss\";\n\nhtml {\n  scrollbar-color: var(--scrollbar-thumb) var(--background-body);\n  scrollbar-width: none;\n  font-family: $font-family-base;\n  font-size: $font-size-base;\n  color: $font-color-base;\n}\n\nbody {\n  font-family: $font-family-body;\n  // font-size: $font-size-body;\n  font-size: 1.2rem; // novo-design-tokens is 1.4rem\n  line-height: 1.2;\n  word-wrap: break-word;\n  color: var(--text-main);\n  background: var(--background-body);\n  text-rendering: optimizeLegibility;\n}\n\nbutton,\ninput,\ntextarea {\n  transition: background-color var(--animation-duration) linear, border-color var(--animation-duration) linear,\n    color var(--animation-duration) linear, box-shadow var(--animation-duration) linear, transform var(--animation-duration) ease;\n}\n","// Hyperlinks\n// --------------------------------------------------\na {\n  // color: $font-color-link;\n  color: var(--links);\n  font-weight: 500;\n  text-decoration: none;\n  cursor: pointer;\n  transition: color 200ms ease-in-out;\n  &:visited {\n    color: $font-color-link;\n  }\n  &:active,\n  &:hover {\n    color: darken($font-color-link, 10%);\n  }\n}\n","/**\n * Flex Grid -\n * Version: 0.3.2\n *\n * Simple grid built with flex box and sass.\n *\n * Matthew Simo - matthew.a.simo@gmail.com\n */\n\n/**\n * Grid setup\n *\n * The grid will calculate dimensions based on these two variables:\n * $fg-columns will inform the grid loops how many columns there should be.\n * $fg-gutter will inform the grid loops how big eac column's gutters should be.\n *\n * The grid will name columns, rows, offsets based on these three variables:\n * $fg-class-row string used for the row class\n * $fg-class-col string used for the column class\n * $fg-class-off string used for the offset class\n */\n$fg-columns: 12 !default;\n$fg-gutter: 0rem !default;\n$fg-class-row: \"row\" !default;\n$fg-class-col: \"col\" !default;\n$fg-class-off: \"off\" !default;\n\n/**\n * Break point namespace object\n *\n * Set the default namespace object with these defaults with the\n * understanding that you can pass in whatever you might require for your site.\n *\n * $fg-breakpoints is a Sass list with nested lists inside. Each sub list defines two things.\n * 1. The namespace for that breakpoint. (Required) (i.e. xs, sm, md, lg)\n * 2. The min-width measurement for the breakpoint for that namespace. (i.e. 48em, 62em, 75em)\n *\n * Note: These should be in the proper order (at least till libsass handles map keys properly).\n *\n * Note: If the measurement is left out then it will be skipped when generating\n * the grid and applied to global styles.\n *\n */\n$fg-breakpoints: ((xs), (sm, 768px), (md, 992px), (lg, 1200px)) !default;\n\n/**\n * Class Name Defaults\n *\n * Define class names for columns, rows and offsets in case compatibility with other\n * libraries is necessary.\n\n/**\n * Calculate column size percentage\n */\n@function get-col-percent($column-num) {\n  @return $column-num / $fg-columns * 100%;\n}\n\n/**\n * Spacing mixin to create uniform margin/padding\n */\n@mixin spacing($value, $type: margin, $orientation: vertical) {\n  @if $orientation == vertical {\n    #{ $type }-top: $value;\n    #{ $type }-bottom: $value;\n  } @else {\n    #{ $type }-left: $value;\n    #{ $type }-right: $value;\n  }\n}\n\n/**\n * Row wrapper class, flex box parent.\n */\n.#{ $fg-class-row } {\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  @include spacing((-$fg-gutter), margin, horizontal);\n}\n\n%#{ $fg-class-col } {\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: column;\n  flex-grow: 0;\n  flex-shrink: 0;\n  @include spacing(($fg-gutter), padding, horizontal);\n}\n\n%#{ $fg-class-col }-flex {\n  @extend %#{ $fg-class-col };\n  flex-grow: 1;\n  flex-basis: 0;\n  max-width: 100%;\n}\n\n/**\n * Generate a set of grid column classes using a namespace\n *\n * .#{ $fg-class-col }-[namespace] for intelligent column division\n * .#{ $fg-class-col }-[namespace]-[number] for a column that covers a specific number of columns (e.g. 1-12 by default)\n * .#{ $fg-class-off }-[namespace]-[number] for pushing a col a specific number of columns (e.g. 1-11 by default)\n */\n@mixin grid-base($namespace) {\n  .#{ $fg-class-col }-#{$namespace} {\n    @extend %#{ $fg-class-col }-flex;\n  }\n  @for $i from 1 through $fg-columns {\n    .#{ $fg-class-col }-#{$namespace}-#{$i} {\n      @extend %#{ $fg-class-col };\n    }\n  }\n}\n\n@mixin grid-sizes($namespace) {\n  // Run the loop for as many columns as speficied\n  @for $i from 1 through $fg-columns {\n    .#{ $fg-class-col }-#{$namespace}-#{$i} {\n      flex-basis: get-col-percent($i);\n      max-width: get-col-percent($i);\n    }\n  }\n  @for $i from 1 through $fg-columns {\n    // Don't include the .off class for the last column\n    @if ($i != $fg-columns) {\n      .#{ $fg-class-off }-#{$namespace}-#{$i} {\n        margin-left: get-col-percent($i);\n      }\n    }\n  }\n}\n\n/**\n * Build the grid in two steps, to help minimize file size\n * Step 1, for each namespace, create the grid-base\n * Step 2, for each namespace, wrap the col width/offset measurements in their breakpoint media query\n */\n@each $val in $fg-breakpoints {\n  $namespace: nth($val, 1);\n  @include grid-base($namespace);\n}\n\n@each $val in $fg-breakpoints {\n  @if length($val) == 1 {\n    $namespace: nth($val, 1);\n    @include grid-sizes($namespace);\n  } @else {\n    $namespace: nth($val, 1);\n    $size: nth($val, 2);\n    @media only screen and (min-width: #{$size}) {\n      @include grid-sizes($namespace);\n    }\n  }\n}\n","@import \"./variables.scss\";\n@import \"../elements/common/typography/text.mixins.scss\";\n\n// Headers\n// --------------------------------------------------\nh1 {\n  font-size: 2.5rem;\n  font-weight: 400;\n  margin: 0;\n  padding: 0.45rem 0 0.35rem;\n}\n\nh2 {\n  font-size: 2rem;\n  font-weight: 500;\n  margin: 0;\n  padding: 0.75rem 0 0.3rem;\n}\n\nh3 {\n  font-size: 1.75rem;\n  font-weight: 400;\n  margin: 0;\n  padding: 0.6rem 0 0.4rem;\n}\n\nh4 {\n  font-size: 1.375rem;\n  font-weight: 400;\n  margin: 0;\n  padding: 0.75rem 0 0.5rem;\n}\n\nh5 {\n  font-size: 1.125rem;\n  font-weight: 700;\n  margin: 0;\n  padding: 0.75rem 0 0.25rem;\n  text-transform: uppercase;\n  color: var(--text-muted);\n}\n\nh6 {\n  font-size: 1.125rem;\n  font-weight: 500;\n  margin: 0;\n  padding: 0.75rem 0 0.25rem;\n}\n\n// Body And Paragraph\n// --------------------------------------------------\np {\n  font-size: 1.4rem;\n  line-height: 1.375em;\n  margin: 0;\n  padding: 0.25rem 0 0.55rem;\n}\n\n// h1,\n// h2,\n// h3,\n// h4,\n// h5,\n// h6,\n// strong {\n//   color: var(--text-bright);\n// }\n\nq::before {\n  content: none;\n}\n\nq::after {\n  content: none;\n}\n\nblockquote,\nq {\n  border-left: 4px solid var(--focus);\n  margin: 1.5em 0;\n  padding: 0.5em 1em;\n  font-style: italic;\n}\n\nblockquote > footer {\n  font-style: normal;\n  border: 0;\n}\n\nblockquote cite {\n  font-style: normal;\n}\n\naddress {\n  font-style: normal;\n}\n\nmark {\n  background-color: var(--highlight);\n  border-radius: 2px;\n  padding: 0 2px 0 2px;\n  color: #000;\n}\n\n// Special Classes\n// --------------------------------------------------\n.caption {\n  font-size: 1.125rem;\n  font-weight: 700;\n  text-transform: uppercase;\n  opacity: 0.55;\n  letter-spacing: 0.1px;\n}\n\nh1.novo-section-header,\nh2.novo-section-header,\nh3.novo-section-header,\nh4.novo-section-header,\nh5.novo-section-header,\nh6.novo-section-header {\n  background: var(--background-bright, $color-white);\n  box-sizing: content-box;\n  font-weight: 400;\n  display: flex;\n  width: 100%;\n  padding: 1rem 0.5rem 1rem 1.5rem;\n  margin-left: -1.5rem;\n  margin-right: -1.5rem;\n  > i {\n    display: flex;\n    margin-right: 10px;\n    &.bhi-section {\n      // Special case for icons that are not vertically centered\n      margin-top: -0.2rem;\n    }\n  }\n}\n\n// Utility classes\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nspan {\n  @include base-text-rules();\n}\n","@import \"../../../styles/variables.scss\";\n\n@mixin text-disabled($property) {\n  pointer-events: none;\n  color: $disabled;\n}\n\n@mixin base-text-rules() {\n  transition: 0.2s ease-out;\n\n  &.text-nowrap {\n    white-space: nowrap;\n  }\n  &.text-ellipsis {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n\n  @include text-sizing();\n  @include text-colors();\n  @include text-margins();\n  @include text-lengths();\n  @include text-weights();\n}\n\n@mixin text-sizing() {\n  // Controls font-size of text\n  &.text-size-default {\n    font-size: inherit;\n  }\n  &.text-size-body {\n    font-size: $font-size-body;\n  }\n  &.text-size-xs {\n    font-size: $font-size-xs;\n  }\n  &.text-size-sm {\n    font-size: $font-size-sm;\n  }\n  &.text-size-md {\n    font-size: $font-size-md;\n  }\n  &.text-size-lg {\n    font-size: $font-size-lg;\n  }\n  &.text-size-xl {\n    font-size: $font-size-xl;\n  }\n  &.text-size-2xl {\n    font-size: $font-size-2xl;\n  }\n  &.text-size-3xl {\n    font-size: $font-size-3xl;\n  }\n  &.text-size-smaller {\n    font-size: 0.8em;\n  }\n  &.text-size-larger {\n    font-size: 1.2em;\n  }\n}\n\n@mixin text-weights() {\n  // Font Weight Utils\n  &.text-weight-hairline {\n    font-weight: 100;\n  }\n  &.text-weight-thin {\n    font-weight: 200;\n  }\n  &.text-weight-light {\n    font-weight: 300;\n  }\n  &.text-weight-normal {\n    font-weight: 400;\n  }\n  &.text-weight-medium {\n    font-weight: 500;\n  }\n  &.text-weight-semibold {\n    font-weight: 600;\n  }\n  &.text-weight-bold {\n    font-weight: 700;\n  }\n  &.text-weight-extrabold {\n    font-weight: 800;\n  }\n  &.text-weight-heavy {\n    font-weight: 900;\n  }\n  &.text-weight-lighter {\n    font-weight: lighter;\n  }\n  &.text-weight-bolder {\n    font-weight: bolder;\n  }\n}\n\n@mixin text-lengths() {\n  &.text-length-small {\n    max-width: 40ch;\n  }\n  &.text-length-medium {\n    max-width: 55ch;\n  }\n  &.text-length-large {\n    max-width: 70ch;\n  }\n}\n\n@mixin text-colors() {\n  @include theme-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n    &.text-color-#{$name} {\n      color: $color;\n    }\n  }\n}\n\n@mixin text-margins() {\n  &.margin-before {\n    margin-top: 0.4rem;\n  }\n\n  &.margin-after {\n    margin-bottom: 0.8rem;\n  }\n}\n\n@mixin novo-body-text() {\n  display: inline;\n  font-weight: normal;\n  color: inherit;\n  font-size: var(--font-size-text);\n  @include base-text-rules();\n}\n\n@mixin novo-title-text() {\n  font-weight: 600;\n  line-height: 1.5;\n  color: var(--text-main, $dark);\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  font-size: var(--font-size-title);\n  @include base-text-rules();\n}\n\n@mixin novo-label-text() {\n  font-weight: 500;\n  text-transform: uppercase;\n  word-break: word-break;\n  overflow-wrap: break-word;\n  line-height: 1.375;\n  color: var(--text-muted);\n  font-size: var(--font-size-label);\n  @include base-text-rules();\n}\n\n@mixin novo-caption-text() {\n  font-size: var(--font-size-caption);\n  font-weight: 400;\n  line-height: 1.375;\n  color: var(--text-muted, $dark);\n  @include base-text-rules();\n}\n","// Iconography\n// --------------------------------------------------\n$icon-height: 1.7rem; // 24px Base background size\n$icon-width: 1.7rem; // 24px Base background size\n$base-icon-size: 1.4rem; //18px Base font-size\ni {\n  font-size: $base-icon-size;\n  display: inline-table;\n  flex: 0 0 auto;\n  &[theme] {\n    &[theme=\"contained\"] {\n      font-size: 0.8rem;\n      border-radius: 0.55rem;\n      height: $icon-height;\n      width: $icon-width;\n      text-align: center;\n      &:before {\n        vertical-align: baseline;\n        line-height: 1.8;\n      }\n      @each $entity, $color in $entity-colors {\n        &.#{$entity} {\n          color: $white;\n          background: $color;\n        }\n      }\n      @each $key, $color in $analytics-colors {\n        &.#{$key} {\n          color: $white;\n          background: $color;\n        }\n      }\n      &.large {\n        font-size: 1.2rem;\n      }\n    }\n    &[theme=\"entity\"] {\n      @each $entity, $color in $entity-colors {\n        &.#{$entity} {\n          color: $color;\n          background: transparent;\n        }\n      }\n    }\n  }\n}\n\n// Nested icons\nfigcaption,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nspan,\n.novo-text,\n.novo-title,\n.novo-label,\n.novo-caption {\n  > i {\n    font-size: 1.1em;\n    margin: 0 0.5em 0 0;\n    padding: 0;\n    &:before {\n      vertical-align: middle !important;\n    }\n    &[theme] {\n      &[theme=\"contained\"] {\n        font-size: 0.8rem;\n        vertical-align: 0.1rem;\n        &:before {\n          vertical-align: baseline;\n          line-height: $icon-width;\n          display: block;\n        }\n      }\n    }\n  }\n}\n","@import \"~novo-design-tokens/scss\";\n\n// TODO: DEPRECATE COLORS AND USE TOKENS\n\n// Colors\n// --------------------------------------------------\n$black: $color-black;\n$white: $color-white;\n$grey: $color-grey;\n$off-white: $color-off-white;\n$light: $color-light;\n$navy: $color-navy;\n$pulse: $color-pulse;\n// New grays below are being added as placeholder.\n// Usages of similar shades will be replaced in App before removing similar legacy variables.\n$sand: $color-sand;\n$silver: $color-silver;\n$stone: $color-stone;\n$ash: $color-ash;\n$slate: $color-slate;\n$charcoal: $color-charcoal;\n$midnight: $color-midnight;\n\n// Analytics Colors\n$aqua: $color-aqua;\n$ocean: $color-ocean;\n$mint: $color-mint;\n$grass: $color-grass;\n$sunflower: $color-sunflower;\n$bittersweet: $color-bittersweet;\n$grapefruit: $color-grapefruit;\n$carnation: $color-carnation;\n$lavender: $color-lavender;\n$mountain: $color-mountain;\n// Entity Colors\n$company: $color-company;\n$candidate: $color-candidate;\n$lead: $color-lead;\n$contact: $color-contact;\n$opportunity: $color-opportunity;\n$job: $color-job;\n$submission: $color-submission;\n$sendout: $color-sendout;\n$placement: $color-placement;\n$note: $color-note;\n$contract: $color-contract;\n$jobCode: $color-job-code;\n$earnCode: $color-earn-code;\n$invoiceStatement: $color-invoice-statement;\n$billableCharge: $color-billable-charge;\n$payableCharge: $color-payable-charge;\n// Color Applications\n$dark: $color-dark;\n$navigation: $color-navigation;\n$positive: $ocean;\n$success: $grass;\n$negative: $grapefruit;\n$warning: $sunflower;\n$background: $off-white;\n$background-dark: #e2e2e2;\n$neutral: $color-neutral;\n$empty: $color-empty;\n$disabled: $color-stone;\n\n// Spacing\n// --------------------------------------------------\n$spacing-rules: (\n  \"none\": $spacing-none,\n  \"px\": $spacing-px,\n  \"xs\": $spacing-xs,\n  \"sm\": $spacing-sm,\n  \"md\": $spacing-md,\n  \"lg\": $spacing-lg,\n  \"xl\": $spacing-xl,\n);\n\n// Sizes\n// --------------------------------------------------\n$header-height: 5rem;\n// $header-height-px: rem-to-px($header-height);\n\n// Color Maps\n// --------------------------------------------------\n// Analytics Color Map\n$analytics-colors: (\n  \"aqua\": $color-aqua,\n  \"ocean\": $color-ocean,\n  \"mint\": $color-mint,\n  \"grass\": $color-grass,\n  \"sunflower\": $color-sunflower,\n  \"bittersweet\": $color-bittersweet,\n  \"grapefruit\": $color-grapefruit,\n  \"carnation\": $color-carnation,\n  \"lavender\": $color-lavender,\n  \"mountain\": $color-mountain,\n);\n// Entity Color Map\n$entity-colors: (\n  \"star\": $color-placement,\n  \"person\": $color-contact,\n  \"company\": $color-company,\n  \"candidate\": $color-candidate,\n  \"navigation\": $color-navigation,\n  \"lead\": $color-lead,\n  \"contact\": $color-contact,\n  \"opportunity\": $color-opportunity,\n  \"job\": $color-job,\n  \"submission\": $color-submission,\n  \"sendout\": $color-sendout,\n  \"placement\": $color-placement,\n  \"note\": $color-note,\n  \"task\": $color-neutral,\n  \"distributionList\": $color-neutral,\n  \"credential\": $color-neutral,\n  \"user\": $color-neutral,\n  \"corporateuser\": $color-neutral,\n  \"contract\": $color-contract,\n  \"jobCode\": $color-job-code,\n  \"earnCode\": $color-earn-code,\n  \"billableCharge\": $color-billable-charge,\n  \"payableCharge\": $color-payable-charge,\n  \"invoiceStatement\": $color-invoice-statement,\n);\n// Generic Color Map\n$app-colors: (\n  \"positive\": $color-ocean,\n  \"success\": $color-grass,\n  \"negative\": $color-grapefruit,\n  \"warning\": $color-sunflower,\n  \"background\": $color-off-white,\n  \"black\": $color-black,\n  \"white\": $color-white,\n  \"grey\": $color-grey,\n  \"off-white\": $color-off-white,\n  \"dark\": $color-dark,\n  \"light\": $color-light,\n  \"pulse\": $color-pulse,\n  \"neutral\": $color-neutral,\n  \"empty\": $color-empty,\n  \"sand\": $color-sand,\n  \"silver\": $color-silver,\n  \"stone\": $color-stone,\n  \"ash\": $color-ash,\n  \"slate\": $color-slate,\n  \"charcoal\": $color-charcoal,\n);\n// Loading Dot Color Map\n$dot-colors: (\n  \"ocean\": $color-ocean,\n  \"lavender\": $color-lavender,\n  \"grapefruit\": $color-grapefruit,\n  \"sunflower\": $color-sunflower,\n  \"mint\": $color-mint,\n);\n// Dark map\n$dark-colors: (\n  \"company\": $color-company,\n  \"candidate\": $color-candidate,\n  \"navigation\": $color-navigation,\n  \"lead\": $color-lead,\n  \"contact\": $color-contact,\n  \"opportunity\": $color-opportunity,\n  \"job\": $color-job,\n  \"jobCode\": $color-job-code,\n  \"earnCode\": $color-earn-code,\n  \"sendout\": $color-sendout,\n  \"placement\": $color-placement,\n  \"task\": $color-neutral,\n  \"distributionList\": $color-neutral,\n  \"credential\": $color-neutral,\n  \"user\": $color-neutral,\n  \"corporateuser\": $color-neutral,\n  \"aqua\": $color-aqua,\n  \"ocean\": $color-ocean,\n  \"mint\": $color-mint,\n  \"grass\": $color-grass,\n  \"sunflower\": $color-sunflower,\n  \"bittersweet\": $color-bittersweet,\n  \"grapefruit\": $color-grapefruit,\n  \"carnation\": $color-carnation,\n  \"lavender\": $color-lavender,\n  \"positive\": $color-ocean,\n  \"success\": $color-grass,\n  \"negative\": $color-grapefruit,\n  \"warning\": $color-sunflower,\n  \"black\": $color-black,\n  \"dark\": $color-dark,\n  \"pulse\": $color-pulse,\n  \"neutral\": $color-neutral,\n  \"navy\": $color-navy,\n  \"contract\": $color-contract,\n  \"mountain\": $color-mountain,\n  \"billableCharge\": $color-billable-charge,\n  \"payableCharge\": $color-payable-charge,\n  \"invoiceStatement\": $color-invoice-statement,\n  \"submission\": $color-submission,\n  \"note\": $color-note,\n  \"ash\": $color-ash,\n  \"slate\": $color-slate,\n  \"charcoal\": $color-charcoal,\n  \"midnight\": $color-midnight,\n);\n// Light map\n$light-colors: (\n  \"background\": $color-off-white,\n  \"background-dark\": $color-background-dark,\n  \"white\": $color-white,\n  \"grey\": $color-grey,\n  \"off-white\": $color-off-white,\n  \"light\": $color-light,\n  \"empty\": $color-empty,\n  \"disabled\": $color-disabled,\n  \"sand\": $color-sand,\n  \"silver\": $color-silver,\n  \"stone\": $color-stone,\n);\n// All colors\n$all-colors: map-merge($dark-colors, $light-colors);\n\n// Typography\n// --------------------------------------------------\n$base-font-size: 13px;\n$base-font-family: \"Montserrat\", sans-serif;\n$base-font-color: $dark;\n$base-line-height: 1rem;\n$alt-font-family: Georgia, \"Times New Roman\", Times, serif;\n\n// Whiteframes\n// -------------------------\n$whiteframe-shadow-z1: 0px 2px 5px 0 rgba(0, 0, 0, 0.26) !default;\n$whiteframe-zindex-z1: 1 !default;\n$whiteframe-shadow-z2: 0px 8px 17px rgba(0, 0, 0, 0.2) !default;\n$whiteframe-zindex-z2: 2 !default;\n$whiteframe-shadow-z3: 0px 4px 10px rgba(0, 0, 0, 0.15) !default;\n$whiteframe-zindex-z3: 3 !default;\n$whiteframe-shadow-z4: 0px 16px 28px 0 rgba(0, 0, 0, 0.22) !default;\n$whiteframe-zindex-z4: 4 !default;\n$whiteframe-shadow-z5: 0px 27px 24px 0 rgba(0, 0, 0, 0.2) !default;\n$whiteframe-zindex-z5: 5 !default;\n\n// Z-indexes\n// -------------------------\n// Function to call z-indexes.\n// Usage: z-index: z('modal');\n@function z($layer) {\n  $z-layers: (\n    \"max\": 9001,\n    \"overlay\": 1000,\n    \"modal\": 500,\n    \"tooltip\": 100,\n    \"toast\": 90,\n    \"dialogue\": 80,\n    \"bottom-sheet\": 70,\n    \"aside\": 61,\n    \"sidenav\": 60,\n    \"backdrop\": 50,\n    \"fab\": 20,\n    \"above\": 10,\n    \"default\": 1,\n    \"below\": -1,\n    \"bottom\": -9000,\n  );\n\n  @if not map-has-key($z-layers, $layer) {\n    @warn 'No layer found for `#{$layer}` in $z-layers map. Property omitted.';\n  }\n\n  @return map-get($z-layers, $layer);\n}\n\n// Easing Curves\n// -------------------------\n// Usage: html-tag { @include ease('all', 'swift-ease-in-out'); }\n$easing-curves: (\n  \"swift-ease-out\": (\n    \"duration\": 400ms,\n    \"curve\": \"cubic-bezier(0.25, 0.8, 0.25, 1)\",\n  ),\n  \"swift-ease-in\": (\n    \"duration\": 300ms,\n    \"curve\": \"cubic-bezier(0.55, 0, 0.55, 0.2)\",\n  ),\n  \"swift-ease-in-out\": (\n    \"duration\": 500ms,\n    \"curve\": \"cubic-bezier(0.35, 0, 0.25, 1)\",\n  ),\n);\n\n@function map-deep-get($map, $keys...) {\n  @each $key in $keys {\n    $map: map-get($map, $key);\n  }\n\n  @return $map;\n}\n\n@mixin ease($target, $curve-name) {\n  $duration: map-deep-get($easing-curves, $curve-name, \"duration\");\n  $curve: map-deep-get($easing-curves, $curve-name, \"curve\");\n  transition: #{$target $duration $curve};\n}\n","// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: z(overlay);\n$cdk-z-index-overlay: z(overlay);\n$cdk-z-index-overlay-backdrop: z(backdrop);\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.6);\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container,\n  .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  .cdk-overlay-backdrop {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 0.48;\n      backdrop-filter: blur(4px);\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from\n    // capturing the user's mouse scroll events. Since we also can't use something like\n    // `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at\n    // all and using `opacity` to make the element transparent.\n    &,\n    &.cdk-overlay-backdrop-showing {\n      opacity: 0;\n    }\n  }\n\n  // Used when disabling global scrolling.\n  .cdk-global-scrollblock {\n    position: fixed;\n\n    // Necessary for the content not to lose its width. Note that we're using 100%, instead of\n    // 100vw, because 100vw includes the width plus the scrollbar, whereas 100% is the width\n    // that the element had before we made it `fixed`.\n    width: 100%;\n\n    // Note: this will always add a scrollbar to whatever element it is on, which can\n    // potentially result in double scrollbars. It shouldn't be an issue, because we won't\n    // block scrolling on a page that doesn't have a scrollbar in the first place.\n    overflow-y: scroll;\n  }\n}\n","$table-bg-accent: var(--background-bright, $off-white);\n$novo-row-horizontal-padding: 20px;\n$table-header-font-weight: 400;\n$breakpoint: 1000px;\n\ncdk-table {\n  display: block;\n  flex: 1;\n  &.expandable {\n    width: fit-content;\n    min-width: 100%;\n  }\n  &.empty {\n    min-height: 62px;\n    max-height: 62px;\n  }\n  > .novo-data-table-row:nth-of-type(odd) {\n    .novo-data-table-cell,\n    .novo-data-table-button-cell,\n    .novo-data-table-dropdown-cell,\n    .novo-data-table-expand-cell,\n    .novo-data-table-checkbox-cell {\n      background-color: var(--background-muted, $sand);\n    }\n    &.active {\n      .novo-data-table-cell,\n      .novo-data-table-button-cell,\n      .novo-data-table-dropdown-cell,\n      .novo-data-table-expand-cell,\n      .novo-data-table-checkbox-cell {\n        background-color: rgba($ocean, 0.15);\n      }\n      + .novo-data-table-detail-row {\n        background-color: rgba($ocean, 0.15);\n      }\n    }\n    + .novo-data-table-detail-row {\n      background-color: var(--background-muted, $sand);\n    }\n  }\n  > .novo-data-table-row:nth-of-type(even) {\n    .novo-data-table-cell,\n    .novo-data-table-button-cell,\n    .novo-data-table-dropdown-cell,\n    .novo-data-table-expand-cell,\n    .novo-data-table-checkbox-cell {\n      background-color: var(--background-body, $white);\n    }\n    &.active {\n      .novo-data-table-cell,\n      .novo-data-table-button-cell,\n      .novo-data-table-dropdown-cell,\n      .novo-data-table-expand-cell,\n      .novo-data-table-checkbox-cell {\n        background-color: rgba($ocean, 0.15);\n      }\n      + .novo-data-table-detail-row {\n        background-color: rgba($ocean, 0.15);\n      }\n    }\n    + .novo-data-table-detail-row {\n      background-color: var(--background-body, $white);\n    }\n  }\n}\n\n.novo-data-table-cell-align-right {\n  text-align: right;\n}\n\n.novo-data-table-header-row,\n.novo-data-table-header-cell {\n  position: relative;\n  z-index: z(default);\n}\n\n.novo-data-table-header-row.fixed-header,\n.novo-data-table-header-cell.fixed-header {\n  position: sticky;\n  top: 0;\n}\n\n.novo-data-table-row,\n.novo-data-table-header-row {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: nowrap;\n  background-color: var(--background-body, $white);\n  