px 5px 26px 4px #{rgba($color, 0.12)}\",\n    15: \"0px 6px 28px 5px #{rgba($color, 0.12)}\",\n    16: \"0px 6px 30px 5px #{rgba($color, 0.12)}\",\n    17: \"0px 6px 32px 5px #{rgba($color, 0.12)}\",\n    18: \"0px 7px 34px 6px #{rgba($color, 0.12)}\",\n    19: \"0px 7px 36px 6px #{rgba($color, 0.12)}\",\n    20: \"0px 8px 38px 7px #{rgba($color, 0.12)}\",\n    21: \"0px 8px 40px 7px #{rgba($color, 0.12)}\",\n    22: \"0px 8px 42px 7px #{rgba($color, 0.12)}\",\n    23: \"0px 9px 44px 8px #{rgba($color, 0.12)}\",\n    24: \"0px 9px 46px 8px #{rgba($color, 0.12)}\"\n  );\n}\n","$small-spinner-size: 1.2em;\n$medium-spinner-size: 1.8em;\n$large-spinner-size: 2.1em;\n\nnovo-spinner {\n  display: inline-block;\n  width: $medium-spinner-size;\n  height: $medium-spinner-size;\n  position: relative;\n\n  &.size-small {\n    width: $small-spinner-size;\n    height: $small-spinner-size;\n  }\n\n  &.size-large {\n    width: $large-spinner-size;\n    height: $large-spinner-size;\n  }\n\n  .dot {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    left: 0;\n    top: 0;\n  }\n\n  .dot:before {\n    content: \"\";\n    display: block;\n    margin: 0 auto;\n    width: 15%;\n    height: 15%;\n    background-color: #333;\n    border-radius: 100%;\n    -webkit-animation: dotFadeDelay 1.2s infinite ease-in-out both;\n    animation: dotFadeDelay 1.2s infinite ease-in-out both;\n  }\n\n  @each $analytics, $color in $analytics-colors {\n    &.#{$analytics} {\n      .dot:before {\n        background-color: $color;\n      }\n    }\n  }\n\n  &.multicolor {\n    @each $name, $color in $analytics-colors {\n      $i: index($analytics-colors, $name $color);\n      .dot:nth-of-type(#{$i}) {\n        &:before {\n          background-color: $color;\n        }\n      }\n    }\n  }\n\n  &.white,\n  &[inverse] {\n    .dot:before {\n      background-color: $white;\n    }\n  }\n\n  .dot2 {\n    -webkit-transform: rotate(30deg);\n    -ms-transform: rotate(30deg);\n    transform: rotate(30de