            color: $positive;\n            cursor: pointer;\n            font-size: 1em;\n            opacity: 0.6;\n            padding: 1rem;\n            flex: 1;\n            &.active {\n              opacity: 1;\n              background-color: $positive;\n              color: $white;\n            }\n          }\n        }\n      }\n    }\n  }\n  .digital--inner {\n    display: flex;\n    text-align: center;\n    align-items: center;\n    justify-content: center;\n  }\n  .digital--clock {\n    color: #fff;\n    font-size: 1.8em;\n    font-weight: 500;\n  }\n  .control-block {\n    display: inline-block;\n  }\n  .digital--period {\n    display: block;\n    color: #fff;\n    cursor: pointer;\n    font-size: 1em;\n    opacity: 0.6;\n    text-transform: uppercase;\n    font-weight: 400;\n    &.active {\n      opacity: 1;\n      font-weight: 600;\n    }\n  }\n  .increments {\n    position: relative;\n    height: 250px;\n    width: 130px;\n    display: flex;\n    flex-flow: row nowrap;\n\n    novo-list {\n      overflow-y: auto;\n      overflow-x: hidden;\n      height: 100%;\n      flex: 1;\n      scroll-snap-type: y mandatory;\n      -ms-overflow-style: none; /* Internet Explorer 10+ */\n      scrollbar-width: none; /* Firefox */\n      &::-webkit-scrollbar {\n        display: none; /* Safari and Chrome */\n      }\n    }\n\n    novo-list-item {\n      padding: 5px 16px;\n      width: 130px;\n      scroll-snap-align: start;\n\n      .list-item {\n        line-height: 19px;\n      }\n      &:focus,\n      &:hover {\n        background: lighten($light, 10%);\n        color: darken($light, 55%);\n      }\n      &.active {\n        background-color: $positive;\n        color: $white;\n        font-weight: 500;\n      }\n    }\n  }\n\n  /* ANALOG CLOCK\n    ----------------------------- */\n  .analog {\n    height: 250px;\n    width: 250px;\n    position: relative;\n    margin: 10% auto;\n  }\n  .analog--inner {\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    width: 100%;\n    position: absolute;\n    transition: transform 125ms linear;\n  }\n  .analog--face {\n    top: 5px;\n    right: 5px;\n    left: 5px;\n    bottom: 5px;\n    position: absolute;\n    border-radius: 50%;\n  }\n  .analog--hand--hours {\n    width: 240px;\n    height: 240px;\n    display: block;\n    position: absolute;\n    top: 0;\n    border-radius: 50%;\n    transform: rotate(0deg);\n    transition: transform 100ms linear;\n    z-index: 1;\n    &:before {\n      content: \" \";\n      width: 2px;\n      top: 0;\n      bottom: 0;\n      left: 30%;\n      margin: 30%;\n      z-index: -1;\n      display: block;\n      position: absolute;\n      transform: rotate(90deg);\n    }\n    .analog--ball {\n      height: 3rem;\n      width: 3rem;\n      display: block;\n      right: 3.2rem;\n      top: 50%;\n      margin-top: -1.5rem;\n      border-radius: 50%;\n      position: absolute;\n      border: 3px solid $positive;\n      background: $positive;\n    }\n    &.hour-12 {\n      transform: rotate(-90deg);\n    }\n    &.hour-1 {\n      transform: rotate(-60deg);\n    }\n    &.hour-2 {\n      transform: rotate(-30deg);\n    }\n    &.hour-3 {\n      transform: rotate(0);\n    }\n    &.hour-4 {\n      transform: rotate(30deg);\n    }\n    &.hour-5 {\n      transform: rotate(60deg);\n    }\n    &.hour-6 {\n      transform: rotate(90deg);\n    }\n    &.hour-7 {\n      transform: rotate(120deg);\n    }\n    &.hour-8 {\n      transform: rotate(150deg);\n    }\n    &.hour-9 {\n      transform: rotate(180deg);\n    }\n    &.hour-10 {\n      transform: rotate(210deg);\n    }\n    &.hour-11 {\n      transform: rotate(240deg);\n    }\n  }\n  .analog--hand--minutes {\n    width: 200px;\n    height: 200px;\n    margin: 20px;\n    display: block;\n    position: absolute;\n    top: 0;\n    border-radius: 50%;\n    transform: rotate(0deg);\n    transition: transform 100ms linear;\n    z-index: 1;\n    &:before {\n      left: 23%;\n      margin: 33%;\n      content: \" \";\n      width: 2px;\n      top: 0;\n      bottom: 0;\n      z-index: -1;\n      display: block;\n      position: absolute;\n      transform: rotate(90deg);\n    }\n    .analog--ball {\n      display: block;\n      top: 50%;\n      border-radius: 50%;\n      position: absolute;\n      height: 2.4rem;\n      width: 2.4rem;\n      right: 4.2rem;\n      margin-top: -1.2rem;\n      border: 2px solid lighten($positive, 10%);\n      background: lighten($positive, 10%);\n      transition: all 160ms ease-in-out;\n      &.between {\n        height: 1px;\n        border-radius: 2em;\n        margin-top: 0;\n      }\n    }\n    &.min-00 {\n      transform: rotate(-90deg);\n    }\n    @for $i from 1 through 9 {\n      $deg: ($i * (30/5) - 90);\n      &.min-0#{$i} {\n        transform: rotate(#{$deg}deg);\n      }\n    }\n    @for $i from 10 through 59 {\n      $deg: ($i * (30/5) - 90);\n      &.min-#{$i} {\n        transform: rotate(#{$deg}deg);\n      }\n    }\n  }\n  .analog--center {\n    height: 12rem;\n    width: 12rem;\n    border-radius: 50%;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    z-index: 1;\n    margin-top: 1px;\n    margin-left: 1px;\n    display: block;\n    position: absolute;\n    background-color: $background;\n  }\n  .analog--hour,\n  .analog--minute {\n    font-size: 1.6rem;\n    color: #666666;\n    left: 50%;\n    top: 50%;\n    z-index: 3;\n    text-align: center;\n    width: 40px;\n    padding: 8px 0;\n    border-radius: 50%;\n    cursor: pointer;\n    margin-left: -20px;\n    margin-top: -20px;\n    position: absolute;\n    &.active {\n      color: #fff;\n    }\n  }\n  .analog--minute {\n    font-size: 1rem;\n    margin-left: -20px;\n    margin-top: -16px;\n  }\n  .analog--hours,\n  .analog--minutes {\n    width: 250px;\n    height: 250px;\n    float: left;\n    position: relative;\n  }\n  .analog--minutes {\n    position: absolute;\n  }\n  $d: 4.5em;\n  @for $i from 1 through 12 {\n    $deg: ($i * 30 - 90);\n    .analog