\";\n@import \"./elements/simple-table/table\";\n@import \"./elements/tabbed-group-picker/TabbedGroupPicker\";\n\n.bgc-main {\n  background-color: var(--background-main, $color-bright);\n}\n.bgc-alt {\n  background-color: var(--background-bright, $color-white);\n}\n.bgc-body {\n  background-color: var(--background-body, $color-silver);\n}\n.txc-main {\n  color: var(--text-main, $color-dark);\n}\n.txc-bright {\n  color: var(--text-bright, $color-black);\n}\n.txc-muted {\n  color: var(--text-muted, $color-slate);\n}\n\n@include theme-colors() using ($name, $color, $contrast, $tint, $shade, $pale) {\n  .novo-background-#{$name} {\n    background: $color;\n    color: $contrast;\n  }\n  .novo-text-#{$name} {\n    color: $color;\n  }\n\n  .bgc-#{$name} {\n    background: $color;\n    color: $contrast;\n  }\n\n  .bgc-#{$name}-striped {\n    & > *:nth-child(odd) {\n      background: $color;\n    }\n  }\n  .txc-#{$name} {\n    color: $color;\n  }\n}\n\n@each $key, $value in $spacing-rules {\n  .padding-#{$key} {\n    padding: $value;\n  }\n  .px-#{$key} {\n    padding-left: $value;\n    padding-right: $value;\n  }\n  .py-#{$key} {\n    padding-top: $value;\n    padding-bottom: $value;\n  }\n  .margin-#{$key} {\n    margin: $value;\n  }\n  .ml-#{$key},\n  .mx-#{$key} {\n    margin-left: $value;\n  }\n  .mr-#{$key},\n  .mx-#{$key} {\n    margin-right: $value;\n  }\n  .mt-#{$key},\n  .my-#{$key} {\n    margin-top: $value;\n  }\n  .mb-#{$key},\n  .my-#{$key} {\n    margin-bottom: $value;\n  }\n}\n\n.modal-overlay-backdrop,\n.aside-overlay-backdrop {\n  background-color: rgba(0, 0, 0, 0.8);\n}\n\n.aside-overlay-panel {\n  position: relative;\n  // z-index: 1001;\n}\n\n@keyframes iconEnter {\n  0% {\n    opacity: 0;\n    transform: scale(0.8);\n  }\n  90% {\n    opacity: 1;\n    transform: scale(1.1);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n// Animations\n// -------------------------\n@keyframes rotate {\n  0% {\n    transform: rotateZ(0deg);\n  }\n\n 